import { notFound } from "next/navigation";
type Post = {
  title: string;
  content: string;
};
interface Posts {
  [slug: string]: Post;
}
const POSTS: Posts = {
  "academic-software": {
    title: "On Building Academic Software",
    content: "UniversityCube was created to serve academic professionals...",
  },
  "trading-strategy": {
    title: "My Trading Bot Strategy",
    content:
      "I designed a trading bot with rules like RSI, stop-loss, and DCA...",
  },
};

export async function generateStaticParams() {
  return Object.keys(POSTS).map((slug) => ({ slug }));
}

export default async function BlogPostPage({
  params,
}: {
  params: Promise<{ slug: string }>;
}) {
  const { slug } = await params;
  const post = POSTS[slug];
  if (!post) return notFound();
  return (
    <article className="prose dark:prose-invert max-w-2xl mx-auto">
      <h1>{post.title}</h1>
      <p>{post.content}</p>
    </article>
  );
}
